import axios from 'axios';
import helpers from '../configs/helpers';
import koa from 'koa';
import compress from 'koa-compress';
import fs from 'fs';
import serve from 'koa-static';
import proxy from 'koa-proxy';
import userAgent from 'koa-useragent';
import request from 'koa-request';
import cors from 'koa-cors';
import logger from 'koa-logging';
import path from 'path';
import render from 'koa-ejs';
import webpack from 'webpack';
import webpackDevMiddleware from 'koa-webpack-dev';
import webpackHotMiddleware from 'koa-webpack-hot-middleware';

import { DESCRIPTION, IMAGE_URL, SERVER_URL, SITE_NAME } from './scripts/constants/constants';
import HtmlCompilerWatcher from '../configs/htmlCompilerWatcher';
import 'babel-polyfill';

const app = koa();
const router = require('koa-router')();
const hostname = process.env.HOSTNAME || 'localhost';
const port = process.env.PORT || 8000;

// axios.get(`${SERVER_URL}/posts`).then((response) => {
//   fs.writeFile('./cache.json', JSON.stringify(response.data), 'utf8', (error) => {
//     console.error(error);
//     fs.readFile('./cache.json', 'utf8', (err, data) => {
//       if (err) throw err;
//       console.log('cache.json', data.toString());
//     });
//   });
// });

let config = require('../configs/webpack.server');

render(app, {
  root: helpers.root('app'),
  layout: 'index',
  viewExt: 'html',
  cache: false,
  debug: true
});

app.use(serve('static'));

router.get('*', function *(next) {
  yield this.render('index', { webpackConfig : config });
});

// function applyMeta(request, data) {
//   data = JSON.parse(data.body);
//   Object.assign(META, {
//     title: data.title,
//     description: data.description,
//     publishedTime: data.created_at,
//     modified_time: data.updated_at,
//     url: request.href
//   });
//   compiler.apply(new HtmlCompilerWatcher(META));
// }

app.use(cors());

app.use(router.routes());

app.use(router.allowedMethods());

app.use(compress());

app.use(userAgent());

app.listen(port, (...args) => {
  // console.log('args: ', ...args);
  console.info('==> âœ…  Server is listening');
  console.info('==> ðŸŒŽ  Go to http://%s:%s', hostname, port);
});

if (__DEV__) {
  if (module.hot) {
    console.log('[HMR] Waiting for server-side updates');
    module.hot.accept();
    module.hot.addStatusHandler((status) => {
      if (status === 'abort') {
        setTimeout(() => process.exit(0), 0);
      }
    });
  }
}



